/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import Price from "Common/pricing"

function SavingsInline({ amount, type }) {
  const SavingsInlineCSS = css`
    font-size: 15px;
    font-weight: normal;
    margin-right: 6px;
  `

  return type === "fixed" ? (
    <span css={SavingsInlineCSS}>
      (<Price price={amount} /> {wp.i18n.__("off", "shopwp")})
    </span>
  ) : (
    <span css={SavingsInlineCSS}>
      ({amount}% {wp.i18n.__("off", "shopwp")})
    </span>
  )
}

export default SavingsInline
